.Vertice {
  display: -webkit-box;
  -webkit-box-pack: center;
  -webkit-box-align: center;
  background-color: #00abd7;
  width: 50px;
  height: 50px;
  border-radius: 50%; }

.Vertice input {
  display: -webkit-box;
  text-align: center;
  width: 80%;
  border: none;
  background: transparent;
  outline-style: none; }

.Vertice input:focus {
  background: white; }
